<template>
  <div class="screen-container">
    <div class="screen-body">
      <section class="screen-left">
        <div id="left-top" :class="[fullScreenStatus.rank ? 'fullscreen' : '']">
          <Rank ref="rank"></Rank>
          <div class="resize">
            <img
              @click="changeSize('rank')"
              :src="
                fullScreenStatus.map ? 'imgs/compress.png' : 'imgs/expand.png'
              "
            />
          </div>
        </div>
        <div
          id="left-bottom"
          :class="[fullScreenStatus.trend ? 'fullscreen' : '']"
        >
          <Trend ref="trend"></Trend>
          <div class="resize">
            <img
              @click="changeSize('trend')"
              :src="
                fullScreenStatus.map ? 'imgs/compress.png' : 'imgs/expand.png'
              "
            />
          </div>
        </div>
      </section>
      <section class="screen-middle">
        <div id="middle-top">
          
          <el-row :gutter="20">
            <el-col :span="12">
              <div class="grid-content bg-purple totalnum">
                {{ totalNum.total1 }}
              </div>
            </el-col>
            <el-col :span="12"
              ><div class="grid-content bg-purple totalnum">
                {{ totalNum.total2 }}
              </div></el-col
            >
          </el-row>
        </div>
        <div
          id="middle-bottom"
          :class="[fullScreenStatus.map ? 'fullscreen' : '']"
        >
          <Map ref="map"></Map>
          <div class="resize">
            <img
              @click="changeSize('map')"
              :src="
                fullScreenStatus.map ? 'imgs/compress.png' : 'imgs/expand.png'
              "
            />
          </div>
        </div>
      </section>
      <section class="screen-right">
        <div
          id="right-top"
          :class="[fullScreenStatus.time ? 'fullscreen' : '']"
        >
          <Time ref="time"></Time>
          <div class="resize">
            <img
              @click="changeSize('time')"
              :src="
                fullScreenStatus.map ? 'imgs/compress.png' : 'imgs/expand.png'
              "
            />
          </div>
        </div>
        <div id="right-bottom" :class="[fullScreenStatus.distance ? 'fullscreen' : '']">
          <Distance ref="distance"></Distance>
          <div class="resize" >
            <img
              @click="changeSize('distance')"
              :src="
                fullScreenStatus.distance ? 'imgs/compress.png' : 'imgs/expand.png'
              "
            />
          </div>
        </div>
      </section>
    </div>
  </div>
</template>

<script>
import Map from "@/components/charts/Map";
import Time from "@/components/charts/Time";
import Rank from "@/components/charts/Rank";
import Trend from "@/components/charts/Trend";
import Distance from "@/components/charts/Distance";

export default {
  created() {},
  destroyed() {},
  data() {
    return {
      fullScreenStatus: {
        map: false,
        time: false,
        rank: false,
        trend: false,
        distance: false,
      },
      totalNum: {
        total1: 1000,
        total2: 1000,
        total3: 1000,
      },
    };
  },
  methods: {
    // 全屏的方法
    changeSize(chartName) {
      this.fullScreenStatus[chartName] = !this.fullScreenStatus[chartName];
      this.$nextTick(() => {
        this.$refs[chartName].screenAdapter();
      });
    },
  },
  components: {
    Map,
    Time,
    Rank,
    Trend,
    Distance,
  },
};
</script>
<style lang="less" scoped>
// 全屏样式的定义
.fullscreen {
  position: fixed !important;
  top: 0 !important;
  left: 0 !important;
  width: 100% !important;
  height: 100% !important;
  margin: 0 !important;
  z-index: 100;
  background-color: white;
}

.screen-container {
  width: 100%;
  // height: 750px;
  height: 100%;
  padding: 20px 20px;
  color: black;
  box-sizing: border-box;
}

.screen-body {
  width: 100%;
  height: 100%;
  display: flex;
  .screen-left {
    height: 100%;
    width: 27.6%;
    #left-top {
      height: 53%;
      position: relative;
    }
    #left-bottom {
      height: 43%;
      margin-top: 25px;
      position: relative;
    }
  }
  .screen-middle {
    height: 100%;
    width: 41.5%;
    margin-left: 1.6%;
    margin-right: 1.6%;
    #middle-top {
      width: 100%;
      height: 25%;
      position: relative;
      border: 1px solid black;
      .totalnum {
        text-align: center;
      }
    }
    #middle-bottom {
      margin-top: 25px;
      width: 100%;
      height: 71%;
      position: relative;
    }
  }
  .screen-right {
    height: 100%;
    width: 27.6%;
    #right-top {
      height: 46%;
      position: relative;
    }
    #right-bottom {
      height: 50%;
      margin-top: 25px;
      position: relative;
    }
  }
}
.resize {
  position: absolute;
  width: 25px;
  right: 20px;
  top: 20px;
  cursor: pointer;
  img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
}
</style>
